import { REQUEST_URL } from 'constants/url';
import { https } from './fetch';
export const getAnnouncementList = (url) => https.get(url);
export const getAnnouncement = (announcementId) => https.get(`${REQUEST_URL.announcements}/${announcementId}`);
export const editAnnouncement = async (announcementId, data) => await https.patch(`${REQUEST_URL.announcements}/${announcementId}`, data);
export const addAnnouncement = async (data) => await https.post(`${REQUEST_URL.announcements}`, data);
export const deleteAnnouncement = (announcementId) => https.delete(`${REQUEST_URL.announcements}/${announcementId}`);
